<template>
  <div>
    <el-col :span="14">
      <div class="nav">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>驾驶证管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </el-col>
    <!-- 所有用户信息 -->
    <template>
      <el-table :data="curtableData" style="width: 100%" v-loading="loading">
        <el-table-column label="姓名" width="200" prop="username">
          <!-- <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.date }}</span>
          </template> -->
        </el-table-column>
        <el-table-column prop="date_jsz" label="首次领证日期" width="210">
        </el-table-column>
        <el-table-column prop="first_hz" label="第一次换证日期" width="210">
        </el-table-column>
        <el-table-column prop="second_hz" label="第二次换证日期" width="210">
        </el-table-column>
        <el-table-column prop="call" label="联系电话" width="230">
        </el-table-column>
        <el-table-column label="驾驶证图片" width="230">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="look(scope.$index, scope.row)"
              >查看</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="block">
        <el-pagination
          background
          :page-size="pageSize"
          :current-page="curPage"
          layout="prev, pager, next"
          :total="total"
          @current-change="currentChange"
        ></el-pagination>
      </div>
      <!-- 分页 -->
    </template>
  </div>
</template>
    <!-- alluserinfo end -->

<script>
import moment from "moment";
export default {
  name: "alluser",
  data() {
    return {
      loading: true,
      curPage: 1,
      pageSize: 15,
      tableData: [],
      time: moment().format("YYYY"),
      num: 0,
      num_: 0,
      url: "",
    };
  },
  // 获取用户信息
  created() {
    // 获取文件IP地址
    this.$axios.get("/api/ip").then((res) => {
      this.url = res.data.data;
    });
    let id = "LXuao";

    this.$axios({
      method: "POST",
      url: "/api/finduser",
      data: {
        id: id,
      },
    }).then((res) => {
      let data = res.data.data;
      data.map((e, i) => {
        if (e.date_jsz != "") {
          if (e.date_jsz != "null") {
            this.tableData.push(e);
            let d = e.date_jsz.substring(0, 4);
            e.first_hz = e.date_jsz.replace(d, Number(d) + 6);
            e.second_hz = e.date_jsz.replace(d, Number(d) + 16);
            if (Number(this.time) == Number(d) + 6) {
              this.num = this.num + 1;
            }
          }
          if (e.img_jsz != "") {
            e.img_jsz = this.url + e.img_jsz.replace("\\", "/").substring(7);
          }
        }
      });
      // this.tableData = res.data.data;
      setTimeout(() => {
        this.loading = false;
        if (this.num != 0) {
          this.$notify({
            title: "提示",
            message: "本年有" + this.num + "位车主需要首次更换驾驶证！",
            type: "info",
          });
        }
        if (this.num_ != 0) {
          this.$notify({
            title: "提示",
            message: "本年有" + this.num_ + "位车主需要第二次更换驾驶证！",
            type: "info",
          });
        }
      }, 1000);
    });
  },
  computed: {
    total(i, row) {
      return this.tableData.length;
    },
    curtableData() {
      const curPage = this.curPage;
      const pageSize = this.pageSize;
      return this.tableData.slice((curPage - 1) * pageSize, pageSize * curPage);
    },
  },

  methods: {
    currentChange(val) {
      this.curPage = val;
    },
    look(i, e) {
      if (e.img_jsz == "") {
        this.$alert("暂无图片！", "提示", {
          confirmButtonText: "确定",
        });
      } else {
        window.open(e.img_jsz);
      }
    },
  },
  components: {},
};
</script>
<style scoped>
.el-popper[x-placement^="bottom"] {
  margin-top: -39px;
  left: 800px !important;
}
</style>